<template>
<transition name="slide-fade">
	<div class="cc-main">
		<img class="cc-logo" src="../assets/img/bg.png" alt="">
		<div class="cc-jieshao" @click="goto('aboutus')">平台介绍</div>
		<div class="cc-list">
			<div class="cc-input0">
				<div class="cc-tit">公司全称</div>
				<van-field class="cc-input" v-model="companyname" maxlength="14" placeholder="请输入公司全称" />
			</div>
			<div class="cc-input0 flexstart">
				<div class="cc-input2 cc-l">
					<div class="cc-tit">所属行业</div>
					<van-field class="cc-input" v-model="industry" readonly right-icon="arrow" placeholder="请选择" @click="show = true" />
				</div>
				<div class="cc-input2 cc-r">
					<div class="cc-tit">人员规模</div>
					<van-field class="cc-input" v-model="personnel" readonly right-icon="arrow" placeholder="请选择" @click="show1 = true" />
				</div>
				<div class="cc-line van-hairline--left"></div>
			</div>
			<div class="cc-input0">
				<div class="cc-tit">所属区域</div>
				<van-field class="cc-input" v-model="companyarea" readonly right-icon="arrow" placeholder="请选择省市区" @click="show2 = true" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit">详细地址</div>
				<van-field class="cc-input" v-model="companyaddr" placeholder="请输入公司所在详细地址" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit">联系人姓名</div>
				<van-field class="cc-input" v-model="name" placeholder="请输入企业联系人姓名" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit">联系人电话</div>
				<van-field class="cc-input" v-model="tel" type="tel" placeholder="请输入企业联系人电话" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit">企业介绍</div>
				<van-field class="cc-input" v-model="introduce"  type="textarea" placeholder="请输入企业简介" />
			</div>
			<div class="cc-input0">
				<div class="cc-tit">上传公司环境照片</div>
				<div class="cc-upload1 flexstart">
					<div class="cc-uploaded" v-for="(item,index) in fileList" :key="index">
						<img @click="del(index)" class="cc-del" src="../assets/img/del.png" alt="">
						<img @click="imgPreview(index)" :src="item" alt="">
						<img src="" alt="">
					</div>
					<van-uploader :preview-image="false" v-model="fileList1" :max-count="6" :after-read="afterRead" >
						<div class="cc-uploadicon">
							<van-icon class="cc-icon2" name="plus" />
							<p>添加照片</p>
						</div>
					</van-uploader>
				</div>
			</div>
			<div class="cc-button" @click="sub">提交并去发布职位</div>
		</div>
		<van-popup v-model="show" position="bottom"><van-picker show-toolbar :columns="columns1" @cancel="show = false" @confirm="onConfirm" /></van-popup>
		<van-popup v-model="show1" position="bottom"><van-picker show-toolbar :columns="columns2" @cancel="show1 = false" @confirm="onConfirm1" /></van-popup>
		<van-popup v-model="show2" position="bottom"><van-area :area-list="areaList" :value="districtCode" visible-item-count="9" @confirm="confirmarea" @cancel="show2 = false" /></van-popup>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Uploader ,ImagePreview ,Dialog ,Field ,Icon ,Toast,Popup,Area,Picker } from 'vant';
import areaList from '@/assets/js/area.js'//基础样式
export default {
	name: 'checkin',
	components: {
		[Field.name]: Field,
		[Icon.name]: Icon,
		[Popup.name]: Popup,
		[Toast.name]: Toast,
		[Area.name]: Area,
		[Picker.name]: Picker,
		[Uploader.name]: Uploader,
		[ImagePreview.name]: ImagePreview,
		[Dialog.name]: Dialog,
	},
	data() {
		return {
			areaList,
			show2:false,
			companyname:"",
			companyarea:"",
			companyaddr:"",
			districtCode:'330203',
			province:"浙江省",
			city:"宁波市",
			district:"海曙区",
			tel:"",
			name:"",
			introduce: "",
			
			show:false,
			columns1: [],
			columns01: [],
			industry:"",
			industry1:"",

			show1: false,//薪资
			personnel:"",
			personnel1:"",
			columns2: [],
			columns02: [],
			fileList:[],
			fileList1:[]
		};
	},
	mounted() {
		this.screenIndustry();
		this.screenScale();
	},
	methods: {
		goto(rt){
			this.$router.push({
				path:"/"+rt
			})
		},
		screenIndustry(){
			var that=this;
			this.$http.get('/company/screenIndustry').then(function (res) {
				if (res.code==10000) {
					let arr=[];
					res.data.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns1=arr;
					that.columns01=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		screenScale(){
			var that=this;
			this.$http.get('/company/screenScale').then(function (res) {
				if (res.code==10000) {
					let arr=[];
					res.data.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns2=arr;
					that.columns02=res.data;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		onConfirm(value,index) {
			this.show = false;
			this.industry=value;
			this.industry1=this.columns01[index].id;
		},
		onConfirm1(value,index) {
			this.show1 = false;
			this.personnel=value;
			this.personnel1=this.columns02[index].level;
		},
		//所在地选择
		confirmarea(res){
			this.companyarea=res[0].name+res[1].name+res[2].name
			this.province=res[0].name;
			this.city=res[1].name;
			this.district=res[2].name;
			this.districtCode=res[2].code;
			this.show2 = false;
		},
		afterRead(file) {
			var formData = new FormData()
			formData.append('file', file.file)
			Toast.success("上传中...");
			var that=this;
			this.$http.post('/common/uploadFile',formData).then(function (res) {
				if (res.code==10000) {
					that.fileList.push(res.data.url)
					console.log(that.fileList)
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		del(index){
			Dialog.confirm({
				title: '提示',
				message: '您要删除该图片吗？'
			}).then(() => {
				this.fileList.splice(index,1)
				this.fileList1.splice(index,1)
			}).catch(() => {
			// on cancel
			});
		},
		imgPreview(index){
			let that=this;
			ImagePreview({
				images: that.fileList,
				startPosition: index,
				closeable: true
			})
		},
		//提交数据
		sub(){
			var that=this;
			let test1 = /^1[3-9][0-9]{9}$/
			if (!test1.test(this.tel)) {
				Toast.fail("请填写正确格式的手机号码");
				return false;
			}
			var content="";
			if (this.introduce!='') {
				content=`<p style="font-size:14px;color:#666;line-height:20px;" >${this.introduce}</p>`
			}
			var img="";
			if (this.fileList.length>0) {
				img=this.fileList.toString();
				this.fileList.forEach(function(element) {
					content+=`<img style="width:100%;" src="${element}" alt="">`
				});
			}
			Toast.success('提交中，请稍等...');
			this.$http.post('/company/signUp',{
				name:that.companyname,
				city:that.province+'/'+that.city+'/'+that.district,
				code:that.districtCode,
                address:that.companyaddr,
                business:that.industry1,
                size:that.personnel1,
                linkman:that.name,
                phone:that.tel,
                content:content,
                description:that.introduce,
                img:img
			}).then(function (res) {
				if (res.code==10000) {
					that.$router.replace({
						path:"/home2"
					})
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		}
	}
}
</script>
<style lang="scss" scoped>
.cc-main{
	background:#fff;
	position: relative;
	.cc-jieshao{
		position: absolute;
		right: 0;
		top:160px;
		width: 67px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		background:linear-gradient(319deg,rgba(48,114,252,1) 0%,rgba(98,177,255,1) 100%);
		border-radius:15px 0px 0px 15px;
		font-size:11px;
		color:rgba(255,255,255,1);
	}
}
.cc-logo{
	width:375px;
	height:207px;
}
.cc-list{
	padding:3px 24px 39px;
}
.cc-input0{
	padding:21px 0 15px;
	border-bottom: 1px solid #eee;
	position: relative;
	.cc-tit{
		font-weight:600;
		color:rgba(30,151,168,1);
		line-height:20px;
		margin-bottom: 16px;
	}
	.cc-input{
		width: 100%;
		line-height:22px;
		font-size:16px;
		color:rgba(64,70,91,1);
		font-weight:500;
		padding:0;
	}
	.cc-input2{
		width: 163px;
		&.cc-l{
			padding-right: 16px;
		}
		&.cc-r{
			padding-left: 24px;
		}
	}
	.cc-line{
		position: absolute;
		left: 163px;
		top:28px;
		z-index: 2;
		height: 46px;
		width: 4px;
	}
	.cc-upload1{
		flex-wrap:wrap;
		.cc-uploaded{
			width:95px;
			height:95px;
			border-radius:2px;
			border:1px solid #eee;
			margin:0 14px 10px 0;
			position: relative;
			img{
				width:95px;
				height:95px;
			}
			.cc-del{
				position: absolute;
				right: 0;
				top:0;
				width: 20px;
				height: 20px;
			}
		}
	}
	.cc-uploadicon{
		width:95px;
		height:95px;
		border-radius:2px;
		border:1px solid #eee;
		text-align: center;
		padding-top:26px;
		.cc-icon2{
			font-size:28px;
			color:rgba(133,139,156,1);
		}
		p{
			font-size:12px;
			color:rgba(133,139,156,1);
			line-height:16px;
		}
	}
}
.cc-button{
	width:327px;
	height:44px;
	background:linear-gradient(314deg,rgba(30,151,168,1) 0%,rgba(52,174,191,1) 100%);
	border-radius:2px;
	font-size:18px;
	color:rgba(255,255,255,1);
	line-height:44px;
	text-align: center;
}
</style>